<template lang="pug">
#canvas-container
  .title-container
    p Hello, I'm #[span.highlight-text Wei Shen]
    p Full Stack Developer
    .cta-container(@click="hideParticle")
      p.sub-text View My Work
      .arrow
        svg.bi.bi-arrow-right(
          width="1em",
          height="1em",
          viewBox="0 0 16 16",
          fill="currentColor",
          xmlns="http://www.w3.org/2000/svg"
        )
          path(
            fill-rule="evenodd",
            d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z"
          )

  vue-particles(
    color="#dedede",
    :particleOpacity="0.7",
    :particlesNumber="80",
    shapeType="circle",
    :particleSize="4",
    linesColor="#dedede",
    :linesWidth="1",
    :lineLinked="true",
    :lineOpacity="0.4",
    :linesDistance="150",
    :moveSpeed="3",
    :hoverEffect="true",
    hoverMode="grab",
    :clickEffect="true",
    clickMode="repulse"
  )
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    hideParticle() {
      const canvas = document.getElementById("canvas-container");
      canvas.style.transform = "translateY(-100vh)";
      setTimeout(() => {
        this.$store.commit("setShowContent", true);
      }, 100);
    },
  },
};
</script>

<style lang="sass" scoped>
#canvas-container
  height: 100vh
  width: 100vw
  // background-image: linear-gradient(180deg, #7EC1CC, #3DAEC2, #2B7B8A)
  // background-image: linear-gradient(180deg, #7EC1CC, #2B7B8A)
  background-color: #2B7B8A
  position: absolute
  transition: all 1s ease
  z-index: 1500
  transform: translateY(0)

#particles-js
  width: 100%
  height: 100%

.title-container
  position: absolute
  top: 50%
  left: 50%
  user-select: none
  transform: translate(-50%, -50%)
  font-size: 42px
  text-align: left
  background-color: rgba(43, 123, 138, 0.75)
  // background-color: rgba(0, 0, 0, 0.25)
  // border-radius: 0px
  padding: 40px
  border-radius: 40px
  // border: 1px solid #fff

.highlight-text
  color: #87ceeb
  // color: #23A0D3
  font-weight: bold

.sub-text
  font-size: 20px

.cta-container
  display: flex
  justify-content: center
  align-items: center
  height: 40px
  margin: 20px auto 0px
  border: 1px solid #fff
  width: 230px
  cursor: pointer
  border-radius: 4px
  transition: all 0.25s ease

.cta-container:hover
  background-color: #c7e4e9
  color: #2B7B8A
  .sub-text
    color: #2B7B8A

.arrow
  display: flex
  margin-left: 20px
  width: 24px
  height: 24px
  align-items: center
  justify-content: center
  cursor: pointer
</style>
